.homepage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #243540;
}

.vertical-center {
  background-color: #5cc3ff;
  width: 100%;
  text-align: center;
  margin: 0;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #cccccc;
  color: white;
  text-align: center;
}

.linkedin-logo {
  height: 5vmin;
}